
.backdropp{
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    opacity: 1;
    z-index: 2;
    overflow-y: scroll;

    background-color: rgba(0, 0, 0, 0.2);
    transform: scale(1);
    transition: $transition;
}
.is-openn {
    overflow-y: hidden;
}
.backdropp.is-hiddenn {
    opacity: 0;
    pointer-events: none;
    transform: scale(1.4);
}

.footerModal{
    position: fixed;
    overflow: auto;
    top: 50%;
    left: 50%;
    max-height: calc(100vh - 24px);

    padding: 20px;
    background: #fff;
    box-shadow: 0 1px 3px #0000001f, 2px 2px 1px #00000024, 0 2px 1px #0003;
    border-radius: 4px;

    transform: translate(-50%, -50%);
    transition: $transition;

        @media screen and (min-width: 768px) {
            width: 640px;
            max-height: calc(100vh - 40px);
        }

        @media screen and (min-width: 1024px) {
            width: 960px;
            max-height: calc(100vh - 60px);
        }
}


.container-text-centered{
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;
}
.footerModalClose{
    position: absolute;
    top: 10px;
    right: 10px;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: inherit;
    transform: scale(1) rotate(0deg);
    transition: $transition;
    &:hover,&:focus{
        fill: $btn-color;
        transform: scale(0.9) rotate(180deg);
    }
}
.comand{
    @include font(500, 24, 16);
    margin-bottom: 20px;
}
.comand-list{
    display: flex;
    flex-wrap: wrap;
    margin: -20px;
    margin-left: auto;
    margin-right: auto;
    
}
.comand-item{
    position: relative;
    overflow: hidden;
    width: 250px;
    margin: 20px;
    border-radius: 4px;
    box-shadow: 8px 8px 4px -1px rgba(102, 96, 96, 0.75);
        -webkit-box-shadow: 8px 8px 4px -1px rgba(102, 96, 96, 0.75);
        -moz-box-shadow: 8px 8px 4px -1px rgba(102, 96, 96, 0.75);
    
    &:not(:last-child) {
        margin-bottom: 0px;
    }
    
    &:hover .comand-item-card-action {
        transform: translateX(-100%);
    }
    @media screen and(min-width: 480px) {
        width: 260px;
        height: 360px;
        margin-left: auto;
        margin-right: auto;
    }
    @media screen and (min-width: 768px) {
        &:not(:last-child) {
                margin-bottom: 20px;
            }
    }
        @media screen and(min-width: 1024px) {
            width: 155px;
            height: 193px;
        }
    
}
.comand-item-img{
    height: 100%;
    object-fit: cover;
}
.comand-item-wrapper{
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 0;
    width: 100%;
    color: $secondary-color;
    background-color: rgba(42, 43, 49, 0.8);
    padding: 10px 32px 10px 10px;
}

.comand-title{
    @include font(500, 16, 14);
    margin-bottom: 10px;
}

.comand-link-list{
    position: absolute;
    top: 0;
    right: 0;
    padding: 6px;
}
.comand-link-item-social{
    display: block;
    fill: $secondary-color;
    transition: $transition;
    &:hover,
    &:focus {
        fill: $accent-color;    
    }
}

.comand-item-card-action{
    position: absolute;
        top: 0;
        left: 100%;
        height: 100%;
        width: 100%;
        background-color: rgba(42, 43, 49, 0.8);
        transform: translateX(0);
        transition: $transition;             
}
.comand-item-card-text {
    padding: 10px 5px 58px;
    font-size: 14px;
    line-height: 1.55em;
    letter-spacing: 0.03em;
    color: #fff;
}